import { ORDER_LIST } from '@/config/router.js';
import { loadIntegral } from '@/api/tabbar/todo.js';
import {TO_SCORE_DETAIL} from '@/config/router.js';

export default {
	props: {
		integral: {
			type: Number,
			default: 0
		}
		},
	data() {
		return {
			navList: [
				{
					id: 1,
					icon: 'iconjifen',
					name: '我的积分',
					href: TO_SCORE_DETAIL
				},
				{
					id: 2,
					icon: 'icon19',
					name: '兑换记录',
					href: `${ORDER_LIST}?title=兑换记录`
				}
			],
			// integral: NaN
		};
	},
	created() {
		// this.loadIntegral();
	},
	methods: {
		// toScoreDetail(){
		// 	uni.navigateTo({
		// 		url:TO_SCORE_DETAIL
		// 	})
		// },
		/**
		 * 加载积分
		 */
		loadIntegral: function() {
			loadIntegral().then(res => {
				// console.log(res)
				this.integral = res.integral;
			});
		},
		
		linkToRoute: function(href) {
			uni.navigateTo({
				url: href
			});
		}
	}
};
